<template>
    <div v-if="productData">
        <v-card>
            <v-toolbar color="blue-grey lighten-4" dense card>
                <v-btn icon class="hidden-xs-only" @click="$router.go(-1)">
                    <v-icon>arrow_back</v-icon>
                </v-btn>
                <v-toolbar-title>製品情報</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn color="success" @click="postData()">保存</v-btn>
                <v-btn color="info">コピー</v-btn>
                <v-btn color="error">削除</v-btn>
            </v-toolbar>
            <v-card-text>
                <v-form>
                    <v-layout row wrap>
                        <v-flex ma-1 xs-3>
                            <v-text-field
                                    v-model="productData.id"
                                    label="ID"
                                    required
                                    readonly
                            ></v-text-field>
                        </v-flex>
                        <v-flex ma-1 xs-3>
                            <v-text-field
                                    v-model="productData.customer"
                                    label="顧客"
                                    required
                            ></v-text-field>
                        </v-flex>
                        <v-flex ma-1 xs-3>
                            <v-text-field
                                    v-model="productData.name"
                                    label="製品名"
                                    required
                            ></v-text-field>
                        </v-flex>
                        <v-flex ma-1 xs-3>
                            <v-text-field
                                    v-model="productData.unit_price"
                                    label="単価"
                                    required
                            ></v-text-field>
                        </v-flex>
                        <v-flex ma-1 xs-3>
                            <v-text-field
                                    v-model="productData.unit"
                                    label="単位"
                                    required
                            ></v-text-field>
                        </v-flex>
                    </v-layout>
                </v-form>
            </v-card-text>

        <v-snackbar
                v-model="saveOk"
                color="success"
                :timeout=3000
                :bottom="true"><span>保存が完了しました。</span><v-btn dark flat @click="saveOk = false">閉じる</v-btn>
        </v-snackbar>
        </v-card>
    </div>
</template>

<script>
import axios from 'axios'
import csrdtoken from '../util/csrf-token.js'

export default {
  name: 'ProductDetail',
  data () {
    return {
      productData: null,
      saveOk: false,
    }
  },
  methods: {
    fetchData () {
      axios.get('/api/products/' + this.$route.params.id + '/').then(res => {
        this.productData = res.data
      })
    },
    postData () {
        axios.post('/api/products/', this.productData, {headers: {'X-CSRFToken': csrdtoken("csrftoken")}}).then(res => {
            this.saveOk = true
        })
        .catch((error) => {
            console.log(error)
        })
    },
  },
  created () {
    if (this.$route.params.id != 0) {
        this.fetchData()
    } else {
        this.productData = {"customer": null, "name": null, "unit_price": null, "unit": null}
    }
  },
}

</script>