<template>
    <div v-if="customerData">
        <v-card>
            <v-toolbar color="blue-grey lighten-4" dense card>
                <v-btn icon class="hidden-xs-only" @click="$router.go(-1)">
                    <v-icon>arrow_back</v-icon>
                </v-btn>
                <v-toolbar-title>顧客情報</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn color="success" @click="save()">保存</v-btn>
                <v-btn color="info" @click="loadingDialog = !loadingDialog">コピー</v-btn>
                <v-btn color="error" @click="deleteData()">削除</v-btn>
            </v-toolbar>
            <v-card-text>
                <v-form>
                    <v-layout row wrap>
                        <v-flex ma-1 xs-3>
                            <v-text-field
                                    v-model="customerData.id"
                                    label="ID"
                                    required
                                    readonly
                            ></v-text-field>
                        </v-flex>
                        <v-flex ma-1 xs-3>
                            <v-text-field
                                    v-model="customerData.name"
                                    label="名前"
                                    required
                            ></v-text-field>
                        </v-flex>
                        <v-flex ma-1 xs-3>
                            <v-select
                              v-model="customerData.rank"
                              :items="rankSelectItems"
                              item-text="text"
                              item-value="rank"
                              label="ランク"
                              return-masked-value
                            ></v-select>
                        </v-flex>
                        <v-flex ma-1 xs-3>
                            <v-autocomplete
                                v-model="customerData.user"
                                :items="userSelectItems"
                                item-text="name"
                                item-value="id"
                                label="担当者"
                                return-masked-value
                            ></v-autocomplete>
                        </v-flex>
                        <v-flex ma-1 xs-3>
                            <v-text-field
                                    v-model="customerData.created_at"
                                    label="登録日時"
                                    required
                            ></v-text-field>
                        </v-flex>
                    </v-layout>
                </v-form>

                <div class="subheading">製品</div>
                <product-hot-table :data="customerData.product"></product-hot-table>
            </v-card-text>
        </v-card>
        <v-snackbar
                v-model="saveOk"
                color="success"
                :timeout=4000
                :bottom="true"
        >
            保存しました。
            <v-btn dark flat @click="saveOk = false">閉じる</v-btn>
        </v-snackbar>
        <v-dialog
                v-model="loadingDialog"
                hide-overlay
                persistent
                width="300"
        >
            <v-card
                    color="success"
                    dark
            >
                <v-card-text>
                    保存しています...
                    <v-progress-linear
                            indeterminate
                            color="white"
                            class="mb-0"
                    ></v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import axios from 'axios'
import ProductHotTable from '../components/ProductHotTable.vue'
import DatePicField from '../components/DatePicField.vue'
import csrdtoken from '../util/csrf-token.js'

const rankChoice = [{rank: 'A', text:'A:優先'},{rank: 'B', text:'B:通常'},{rank: 'C', text:'C:維持'},]
const userChoice = [{id: 1, name:'田中俊行'},{id: 2, name:'山田孝之'},{id: 3, name:'ムロツヨシ'},]

export default {
  name: 'CustomerDetail',
  components: {
    ProductHotTable,
    DatePicField,
  },
  data () {
    return {
      rankSelectItems: rankChoice,
      userSelectItems: userChoice,
      customerData: null,
      saveOk: false,
      loadingDialog: false,
    }
  },
  methods: {
    fetchData () {
      axios.get('/api/customers/' + this.$route.params.id + '/').then(res => {
        this.customerData = res.data
      })
    },
    dropEmptyRow() {
        this.customerData.product = this.customerData.product.filter(row => {
            let isNul = false
            for(let col in row) {
                if(row[col] !== null) isNul = true
            }
            return isNul
        })
    },
    save() {
        if(this.customerData.id) {
            this.putData ()
        } else {
            this.postData ()
        }
    },
    postData () {
        this.loadingDialog = true
        this.dropEmptyRow()
        axios.post('/api/customers/', this.customerData, {headers: {'X-CSRFToken': csrdtoken("csrftoken")}}).then(res => {
            this.saveOk = true
            this.loadingDialog = false
        })
        .catch((error) => {
            console.log(error)
        })

    },
    putData () {
        this.loadingDialog = true
        this.dropEmptyRow()
        axios.put('/api/customers/' + this.customerData.id + '/', this.customerData, {headers: {'X-CSRFToken': csrdtoken("csrftoken")}}).then(res => {
            this.saveOk = true
            this.loadingDialog = false
        })
        .catch((error) => {
            console.log(res)
            console.log(error)
        })

    },
    deleteData () {
        this.loadingDialog = true
        this.dropEmptyRow()
        axios.delete('/api/customers/' + this.customerData.id + '/', this.customerData, {headers: {'X-CSRFToken': csrdtoken("csrftoken")}}).then(res => {
            this.saveOk = true
            this.loadingDialog = false
        })
        .catch((error) => {
            console.log(res)
            console.log(error)
        })

    },
  },
  created () {
    if (this.$route.params.id != 0) {
        this.fetchData()
    } else {
        this.customerData = {"id": null, "name": null, "rank": null, "user": null, "product": []}
    }
  }
}

</script>